body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	background: url(/static/media/blue.e5b45a1e.jpg) no-repeat;
	background-position: center;
	background-size: cover;
}
.App {
	border-radius: 15px;
	border: 6px double #9ab3b3;
	background-color: rgba(255, 255, 255, 0.95);
	padding-bottom: 30px;
}	

@media only screen and (max-width: 500px){

	.App {
		width: 75vw;
		min-height: 85vh;
		height: auto;
	}
}
@media only screen and (min-width: 500.1px) and (max-width: 768px) {

	.App {
		width: 450px;
		min-height: 85vh;
		height: auto;
	}
}
@media only screen and (min-width: 768.1px) and (max-width: 1200px) {

	.App {
		width: 650px;
		min-height: 85vh;
		height: auto;
	}
}
@media only screen and (min-width: 1200.1px) {
	.App {
		margin: 10px auto;
		width: 700px;
		min-height: 560px;
	}
}
/* * {
	border: 1px dashed red;
	box-sizing: border-box;
} */

/*
1) small phones  <=500px
2) usual phones 500.1px - 767.9px
3) hi-res phones & pads 768px - 992px
4) big pads & small desktops 992.1px - 1200px
5) desktops >= 1200px
*/
h1, h3 {
	color: #333333;
	font-weight: lighter;
	text-align: center;
}

h3 {
	font-style: italic;
}
@media only screen and (max-width: 768px) {
	h1 {
		font-size: 1.5rem;
	}
	h3 {
		font-size: 1rem;
	}
}
@media only screen and (min-width: 768.1px) {
	h1 {
		font-size: 2.5rem;
	}
	h3 {
		font-size: 1.5rem;
	}
}
* {
	outline: none;
}
.form-wrapper {
	width: 100%;
}
.location {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

input {
	border: 1.5px solid #a8bcba;
	border-radius: 10px;
	line-height: 2rem;
	background-color: rgba(255,255,255, 1);
	box-sizing: border-box;
	text-align: center;
	width: 200px;
}
button {
	border: none;
	color: white;
	font-weight: lighter;
	background-color:#a8bcba;
	transition: 0.6s;
	margin-top: 20px;
}
button:hover {
	cursor: pointer;
	background-color: #7ea0a9;
	transition: 0.6s;
}

.warning {
	text-align: center;
	color: darkred;
}

@media only screen and (max-width: 500px){

	.location {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	
	input {
		margin-top: 20px;
	}


	button {
		height: 30px;
		margin-bottom: 10px;
	}
}
@media only screen and (min-width: 500.1px) and (max-width: 768px) {

	input {
		margin-top: 20px;
	}

	button {
		height: 40px;
	}
}
@media only screen and (min-width: 768.1px) and (max-width: 1200px) {
	
	input {
		margin-top: 20px;
	}

	button {
		height: 50px;
	}
}
@media only screen and (min-width: 1200.1px) {
	.form-wrapper {
		height:70px;
	}
	input {
		margin-top: 20px;
	}
	button {
		height: 50px;
	}
}
ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    list-style: none;
}
li {
    display: inline-block;
    margin: 5px;
    width: 90px;
}
a {
   text-decoration: none;
   color: #333333;
   font-weight: lighter;
}
a:hover {
text-decoration: none;
color: #333333;
}
.nav_item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.weather-wrapper {
    width: 100%;
    text-align: right;
}
.weather-wrapper span {
    font-size: 1.5rem;
    font-weight: lighter;
    margin-right: 20px;
}
#city {
    margin-right: 10px;
}
#country {
    margin-left: 0px;
}
.description {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}
.description img {
    max-width: 160px;
}
@media only screen and (max-width: 1200px) {
    .main-section {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
@media only screen and (min-width: 1200.1px) {
    .main-section {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
}
.wear-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; 
}
.wear-section span {
    margin: 0 15px;
    color: #7ea0a9;
}
h2 {
    font-size: 1.5rem;
    font-style: italic;
    margin: 20px auto;
    line-height: 2rem;
    padding: 2px;
    text-align: center;
    font-weight: lighter;
    background-color: #9ab3b3;
    color: white;

}

.result {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.alert-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.alert {
    display: inline-block;
    margin: 30px auto;
    padding: 10px;
    border: 2px solid #b90b27;
    background-color: rgba(255,99,71, 0.3);
    color: #333333;
}

@media only screen and (max-width: 500px){
	img {
        max-width: 30px;
    }
    .result {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
}
@media only screen and (min-width: 500.1px) and (max-width: 768px) {
	img {
        max-width: 30px;
    }
     .result {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
}
@media only screen and (min-width: 768.1px) and (max-width: 1200px) {
	img {
        max-width: 50px;
    }
     .result {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
}
@media only screen and (min-width: 1200.1px) {
	img {
        max-width: 50px;
    }
}
h4 {
    font-weight: lighter;
    text-align: center;
}
