* {
	outline: none;
}
.form-wrapper {
	width: 100%;
}
.location {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

input {
	border: 1.5px solid #a8bcba;
	border-radius: 10px;
	line-height: 2rem;
	background-color: rgba(255,255,255, 1);
	box-sizing: border-box;
	text-align: center;
	width: 200px;
}
button {
	border: none;
	color: white;
	font-weight: lighter;
	background-color:#a8bcba;
	transition: 0.6s;
	margin-top: 20px;
}
button:hover {
	cursor: pointer;
	background-color: #7ea0a9;
	transition: 0.6s;
}

.warning {
	text-align: center;
	color: darkred;
}

@media only screen and (max-width: 500px){

	.location {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	
	input {
		margin-top: 20px;
	}


	button {
		height: 30px;
		margin-bottom: 10px;
	}
}
@media only screen and (min-width: 500.1px) and (max-width: 768px) {

	input {
		margin-top: 20px;
	}

	button {
		height: 40px;
	}
}
@media only screen and (min-width: 768.1px) and (max-width: 1200px) {
	
	input {
		margin-top: 20px;
	}

	button {
		height: 50px;
	}
}
@media only screen and (min-width: 1200.1px) {
	.form-wrapper {
		height:70px;
	}
	input {
		margin-top: 20px;
	}
	button {
		height: 50px;
	}
}